@import url(https://use.typekit.net/ssy0pws.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.nucleotide h1,
.nucleotide h2,
.nucleotide h3,
.nucleotide h4,
.nucleotide h5,
.nucleotide h6 {
  line-height: 1;
}

.nucleotide header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-logo {
  height: var(--unit3);
}

#mobile-nav {
  display: flex;
}

#desktop-nav {
  display: none;
}

@media screen and (min-width: 768px) {
  #mobile-nav {
    display: none;
  }

  #desktop-nav {
    display: flex;
  }
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

nav ul {
  grid-gap: var(--unit2);
  gap: var(--unit2);
}

nav ul li:last-of-type {
  margin-right: 0 !important;
}

nav ul li:active {
  color: var(--cyan0);
}

.sidenav-hide {
  position: fixed;
  left: 0;
  top: 0;
  color: white;
  transition: all 0.3s;
  transform: translate(-100vw, 0);
  will-change: transform;
  opacity: 0;
}

.sidenav-show {
  position: fixed;
  left: 0;
  top: 0;
  color: white;
  transition: all 0.3s;
  transform: translate(0, 0);
  will-change: transform;
  opacity: 1;
  width: 100vw;
}

.sidenav-hide .overlay {
  height: 100vh;
  width: 100vw;
  background-color: rgba(255, 255, 255, 0);
  opacity: 0;
  transition: all 0.3s ease-out;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 0;
  border: none;
  overflow-y: scroll;
}

.sidenav-show .overlay {
  height: 100vh;
  width: 100vw;
  background-color: rgba(255, 255, 255, 1);
  opacity: 1;
  z-index: -1;
  border: none;
  display: flex;
  justify-content: center;
}

#mobile-nav-button {
  transition: 1s;
  z-index: 2;
}

#mobile-nav-links {
  z-index: 1;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
}

.projects-section > div > a {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: var(--transWhite-50);
  padding: var(--unit2);
  color: var(--transBlueC-75);
  border-radius: var(--unit1);
  box-shadow: 0 3px 6px var(--transBlueC-10);
  transform: scale(1);
}

.projects-section > div > a:hover {
  transform: scale(1.01);
  background: white;
  color: var(--transBlueC-75);
  text-decoration: none;
}

.product-tools,
.design-tools,
.development-tools {
  display: flex;
  flex-wrap: wrap;
  grid-gap: var(--unit1);
  gap: var(--unit1);
}

.product-tools small,
.design-tools small,
.development-tools small {
  padding: var(--unit0) var(--unit1);
  border-radius: var(--unit2);
}

.product-tools small {
  background: rgba(0, 206, 89, 0.25);
}

.design-tools small {
  background: rgba(0, 143, 195, 0.25);
}

.development-tools small {
  background: rgba(255, 96, 96, 0.25);
}

