.clicktool section {
  min-height: calc(100vh - var(--navHeight));
  /* margin: 0 var(--navHeight); */
}

.clicktool section + section {
  min-height: 100vh;
}

.clicktool .geometry {
  height: 100%;
  opacity: 0.5;
}

.clicktool p {
  margin-top: 0;
}

.clicktool-color {
  justify-content: center;
  align-items: center;
  padding: var(--unit1);
}

.design-bible-label {
  position: absolute;
  left: 100%;
}

.hat-label {
  position: absolute;
  top: 100%;
  left: var(--half);
}

.font-bg {
  -webkit-filter: drop-shadow(-3px -3px 6px var(--whiteTrans)),
    drop-shadow(3px 3px 6px var(--blackTrans));
  filter: drop-shadow(3px 3px 2px var(--blackTrans))
