}

.font-bg {
  fill: var(--siteBackground);
}

.visit-clicktool {
  position: relative;
  margin-top: 10rem;
}

/* homeprojectsection.css */

.laptop-project-video-container {
  padding-top: 38%;
  width: 61.75%;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.laptop-project-video {
  height: 102%;
  width: auto;
  position: absolute;
  top: 0;
}

.clicktool-project-button {
  position: absolute;
  top: calc(100% - 10em);
}

.home-project-section .city {
  -webkit-filter: blur(2px);
          filter: blur(2px);
  opacity: 0.5;
  transform: translate(0, -10%);
}

